___CSS_LOADER_EXPORT___.push([module.id, `/* Import Inter Font (ensure you have this in your project or via CDN) */

/* Reset some default styles for consistency across browsers */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Inter', sans-serif;
  background: linear-gradient(135deg, #e0eafc, #cfdef3);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#mainContainer {
  background-color: #ffffff;
  padding: 40px 30px;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
  width: 100%;
  max-width: 400px;
}

/* Weather Data Styles */
#weatherDataContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

#temp {
  font-weight: 700;
  font-size: 4.5rem;
  color: #333333;
}

#desc {
  font-style: italic;
  text-align: center;
  font-size: 1.2rem;
  color: #555555;
}

/* Form Styles */
#inputContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 4px;
}

label {
  margin-bottom: 8px;
  font-weight: 600;
  color: #333333;
}

input[type='text'] {
  width: 100%;
  padding: 12px 16px;
  border: 1.5px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  transition:
    border-color 0.3s ease,
    box-shadow 0.3s ease;
  outline: none;
}

input[type='text']:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

button {
  width: 100%;
  padding: 12px 16px;
  background-color: #007bff;
  border: none;
  border-radius: 8px;
  color: #ffffff;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition:
    background-color 0.3s ease,
    transform 0.2s ease;
}

button:hover {
  background-color: #0056b3;
}

button:active {
  transform: scale(0.98);
}

.hidden {
  display: none;
}

#bodyContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#loader {
  z-index: 1000;
  padding: 20px;
  border-radius: 10px;
  width: fit-content;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 50%;
  height: auto;
}

img {
  height: 25px;
}

/* Responsive Design */
@media (max-width: 480px) {
  #mainContainer {
    padding: 30px 20px;
  }

  #temp {
    font-size: 3.5rem;
  }

  #desc {
    font-size: 1rem;
    text-align: center;
  }

  input[type='text'],
  button {
    font-size: 14px;
    padding: 10px 14px;
  }
}
